<!--  -->
<template>
  <div>
       <Zi goback="true"  data="<" con="密码登陆" bool1="false" main="">

       </Zi>
        <div class="cont">
            <div>
              <input v-model="username" type="text" placeholder="账号">
            </div>
             <div>
              <input v-show="!value2" v-model="password" type="password" placeholder="密码" />
              <input v-show="value2"  v-model="password"  placeholder="密码" />
              <div>
                <mt-switch v-model="value2"></mt-switch>
              </div>  
            
            </div>
             <div>
              <input type="text" v-model="yan" placeholder="验证码">
              <div class="yan" style="margin-top:-0.4rem;">
               <span class="span">
                 <img :src=img alt="" style="margin-left:0.1rem">
               </span>
               <span>
                  <p  @click="reset">看不清</p>
                  <p class="reset" @click="reset">换一种</p>
                 </span> 
              </div>
            </div>
        </div>
        <p class="wen">温馨提示：未注册过的账号，登录时将自动注册</p>
        <p  class="wen">注册过的用户可凭账号密码登录</p>
        <button @click="login">登录</button>
        <router-link to="/forget" tag="p" class="reset reset1">重置密码?</router-link>
      <div class="tishi" v-show="bool2==true">
        <div class="tishi2">
          <span class="tishi_s1"></span><span class="tishi_s2"></span>
        
        </div>
         <p class="tishi_p"> {{val}}</p>
         <button class="que" @click="que">确认</button>
      </div>
  </div>
</template>

<script>
import axios from "axios";
axios.defaults.withCredentials=true
import Zi from "../../../components/header"
export default {
  data () {
    return {
       value1: true,
       value2:false,
       img:"",
       yan:"",
       bool2:'false',
        username:"",
        password:"",
      val:""

    };
  },

  components: {Zi},

  computed: {},

  mounted(){
    this.reset()
   
  },

  created(){},

  methods: {
    // 登陆验证
      

    // 验证码
    reset(){
     axios.post("https://elm.cangdu.org/v1/captchas").then((d)=>{

     this.img =d.data.code
    })
  
    },
    que(){
       this.bool2=false
    },
    login(){
    axios.post("https://elm.cangdu.org/v2/login",
      {captcha_code:this.yan,
      password:this.password,
      username:this.username}
      ).then((d)=>{
        
        if(d.data.status==0){
          if(this.password!==""&&this.password!==""&&this.username!==""){
              this.bool2=true
              this.val=d.data.message
          }
           
        }else{
         var obj={username:d.data.username,user_id:d.data.user_id,login:true}
         this.$store.commit("add",obj)
          this.$router.push("/home")
        }
         
          })
    if(this.username==""){
            this.bool2=true
            this.val="请输入手机号/邮箱/用户名"
          }else if((this.password=="")){
            this.val="请输入密码"
              this.bool2=true
          }else if(this.yan==""){
            this.val="请输入验证码"
            this.bool2=true
          }
     }

}

  }

</script>
<style scoped lang="scss">
.tishi{
  .tishi_p{
  text-align: center;
 margin-top: 0.4rem;
  font-size: 0.3rem;
}
  .que{
    width: 100%;
    display: block
  }
  border-radius: 3%;
  position: fixed;
left: 10%;
top: 12%;
  width: 80%;
  height: 40%;
 border:1px solid #ffffff;
 background: white;
 z-index: inherit;
 .tishi2{
       width: 1.5rem;
    height: 1.5rem;
    border: .05rem solid #f8cb86;
    border-radius: 50%;
    margin: 0 auto;
    margin-top: 0.35rem;
    .tishi_s1{
          width: .05rem;
    height: 0.8rem;
    background-color: #f8cb86;
    margin-left: 0.7rem;
    margin-top: 0.2rem;
    display:block
}

.tishi_s2{
      width: .15rem;
    height: .15rem;
    border: 1px;
    display:block;
    border-radius: 50%;
    margin-top: .2rem;
    background-color: #f8cb86;
     margin-left: 0.65rem;
    margin-top: 0.2rem;
}

 }
}
.cont{
  margin-top: 1rem;
  &>div{
    width: 100%;
    height: 1rem;
    border-bottom: 1px solid #d4d4d4;
    input{
      height:0.5rem;
      margin-left: 0.4rem;
      margin-top: 0.25rem;
      font-size: 0.3rem; 
      color: #ccc;
      border: 1px solid blue;
      border: none
    }
   div{
     float: right;
   margin-top: 0.2rem;
  .el-switch__core{
        width:1rem!important;
  }
   }
  }
}
.wen{
  line-height: 0.5rem;
  margin-left: 0.3rem;
  color: red;
  font-weight: lighter;
}
button{
  display: block;
  height: 0.6rem;
 background-color: #4cd964;
 width: 90%;
 font-size: 0.3rem;
 color: white;
 margin: 0.3rem auto
}
.yan{
  display: flex;
  margin-top: 0.2rem!important;
  span{
    display: block;
  }
}
.reset{
 color: #3b95e9;
}
.reset1{
  float:right;
}
.span{
  line-height:0.5rem;
  margin-right: 0.2rem
}
</style>


